/* General Styles */
body, html {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", monospace;
  color: #333;
}

/* Header Styles */
/* Header Styles */
.header_navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #333333;
  padding: 20px;
  position: sticky;
  top: 0;
  z-index: 2;
  width: 100%;
}

/* Titre principal */
.h1_navbar a {
  text-align: center;
  font-size: 160%;
  font-weight: bold;
  font-family: "Courier New", serif;
  color: white;
  margin-bottom: 1%;
  text-decoration: none;
}

/* Liens de navigation */
.content_navbar {
  display: flex;
  justify-content: space-evenly; 
  align-items: center;
  flex-wrap: nowrap; 
  gap: 2vw;
  width: 100%;
  padding: 0 2vw;
  box-sizing: border-box;
  overflow-x: auto; 
}

.content_navbar a {
  color: red;
  font-size: 1vw; /* taille fluide selon l’écran */
  text-decoration: none;
  white-space: nowrap; /* empêche les sauts de ligne dans les liens */
}

/* Language Selector */
.circular-selector {
  position: absolute;
  top: 2vh;
  right: 2vw;
  width: 6vw;
  height: 6vw;
  border-radius: 50%;
  background-color: #cacaca;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.segment {
  position: absolute;
  width: 50%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 0.9vw;
}

.segment:hover {
  background-color: #e0e0e0;
}

.segment[data-lang="EN"] {
  top: 0;
  left: 0;
  border-top-left-radius: 50%;
}

.segment[data-lang="FR"] {
  top: 0;
  right: 0;
  border-top-right-radius: 50%;
}

.center {
  position: absolute;
  width: 30%;
  height: 30%;
  background-color: #989898;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.8vw;
  color: #333;
}


/* Footer Styles */
.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 2%;
  margin: 1%;
  background-color: #d0c6c6;
  border-radius: 2%;
}

.footer_contact, .footer_social, .footer_desc, .footer_privacy {
  flex: 1 1 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.social_links {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
}

.social_links img {
  width: 40px;
}


/* Adaptation téléphone */
@media (max-width: 600px) {
  .circular-selector {
    width: 50px;
    height: 50px;
  }
  .segment{
    font-size: 2.2vw;
  }
}